export default {

	data() {
		return {
			unionid: '',
			editType: 'edit',
			id: '',
			name: '',
			tel: '',
			isDefault: false,
			themeColor: '#007AFF',
			region: ['', '', ''],
			location: {
				lat: '',
				lng: ''
			},
			address: '',
			country: '',
			sn: ''
		};
	},
	onLoad(option) {
		if (option) {
			this.sn = option.sn
			this.loadData(option.sn)
		}


	},
	methods: {
		//获取当前地理位置

		// 获取订单详情
		loadData(sn) {
			let params = {
				order_sn: sn
			}
			this.$xm.post("/Order/getOrderInfo", params, eve => {
				var res = eve.contact;
				this.address = res.address;
				this.tel = res.phone;
				this.region = [res.province, res.city, res.town];
				this.country = res.country;
				this.name = res.name;
				this.id = res.id
			})

		},
		// 切换地址
		bindRegionChange(e) {
			this.region = e.detail.value;
			this.country = '中国';
			this.location = ""
		},

		// 验证表单
		save() {
			console.log(this.isDefault);
			let data = {
				address: this.address,
				country: "中国",
				id: this.id,
				name: this.name,
				order_sn: this.sn,
				phone: this.tel,
				province: this.region[0],
				city: this.region[1],
				town: this.region[2],
				zip: ""
			};
			if (!data.name) {
				uni.showToast({
					title: '请输入收件人姓名',
					icon: 'none'
				});
				return;
			}
			if (!data.phone) {
				uni.showToast({
					title: '请输入收件人电话号码',
					icon: 'none'
				});
				return;
			}
			var myreg = /^[1][3,4,5,7,8][0-9]{9}$/;
			if (!myreg.test(data.phone)) {
				uni.showToast({
					title: '请输入正确的手机号码！',
					icon: 'none'
				});
				return;
			}
			if (this.region.length == 0) {
				uni.showToast({
					title: '请选择收件地址',
					icon: 'none'
				});
				return;
			}
			if (!data.address) {
				uni.showToast({
					title: '请输入收件人详细地址',
					icon: 'none'
				});
				return;
			}
			this.getEdit(data)


		},
		isDefaultChange() {
			this.isDefault = !this.isDefault;
		},
		// 提交
		getEdit(data) {
			this.$xm.post('/Order/orderaddr', data, res => {
				if (res.code == 2000) {
					uni.showToast({
						title: "提交成功",
						icon: 'none'
					});
					uni.hideLoading();
					uni.navigateBack();
				} else {
					uni.showToast({
						title: res.msg,
						icon: 'none'
					});
				}

			})
		}
	},

};
